import React from 'react';
import { Global, css } from '@emotion/react';
import { colors } from '../theme/theme';
const GlobalCss = () => {
	return (
		<>
			<Global
				styles={css`
					html {
						background: ${colors.black};
					}
					a {
						text-decoration: none;
					}
				`}
			/>
		</>
	);
};
export default GlobalCss;
